import { LightLoading } from './../../../light/light-providers/providers/lightLoading';
import { LightHttp, LightJsonUtil } from './../../../light/light-out/light-out.module';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-new-detail',
  templateUrl: 'new-detail.html',
})
export class NewDetailPage {

  code: any;
  //新闻标题
  newTitle: any = {};
  //新闻内容
  Content: any;
  // 附件
  attachList: Array<any>;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public lightHttp: LightHttp,
    public lightLoading: LightLoading
  ) {
    this.code = navParams.data.item.CODE;
  }

 public ngOnInit(): void {
    this.getNewDetail();
  }
  
  /**
   *获取新闻标题、内容、附件
   *
   * @memberof NewDetailPage
   */
  async getNewDetail() {
    // 启用等待的遮罩层
    this.lightLoading.presentLoading('', () => {
      console.log('new detail loading ok')
    })
    // 同时发起请求
    const [newTitle, Content, attachList] = await Promise.all([
      this.lightHttp.get("News/GetNewsInfo", { objectID: this.code }),
      this.lightHttp.get("News/GetNewsContent", { objectID: this.code }),
      this.lightHttp.get("Attachment/GetAttachments", { objectID: this.code }),
    ])
    // 等三个请求的数据全部返回后再渲染页面
    this.newTitle = LightJsonUtil.toObject(newTitle);
    this.Content = Content;
    this.attachList = LightJsonUtil.toObject(attachList) as Array<any>;
    // 关闭遮罩层
    this.lightLoading.stopLoading()
  }


}



